

.actionContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 0 8px;
    -ms-flex-direction: row;
        flex-direction: row;
}

.actionContainer > div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: inherit;
        align-items: inherit;
    -ms-flex: 2 1;
        flex: 2 1;
}

code {
    background-color: rgba(0, 0, 0, 0.07);
    padding: 1px 4px;
}

code > a {
    color: black;
}


@media only screen and (max-width: 600px) {
    .actionContainer{
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .actionContainer > div {
        margin: 20px 0;
    }
}


